"use strict"

export function initUtils() {
   slidersInit()
}

// ===========================================================================================
// -----------------------------
// SLIDER
// -----------------------------
function slidersInit() {
   if (document.querySelector('.slider-review')) {
      const swiper = new Swiper('.slider-review', {
         loop: true,
         // slidesPerView: 2.5,
         // spaceBetween: 30,

         breakpoints: {
            320: {
               slidesPerView: 1.3,
               spaceBetween: 10,
            },
            430: {
               slidesPerView: 1.5,
               spaceBetween: 15,
               // centteredSlides: true,
            },
            740: {
               slidesPerView: 2.5,
               spaceBetween: 25,
               // centteredSlides: false,
            },
            1050: {
               slidesPerView: 3,
               spaceBetween: 32,
            },
         },
      });
   }
}

// ===========================================================================================
// -----------------------------
// initModal
// -----------------------------
async function initModal() {
   const openButtons = document.querySelectorAll('.open-btn-modal');
   const modalOverlay = document.querySelector('#modal-overlay');
   const modalContent = document.querySelector('#modal-content');
   const fixedElements = document.querySelectorAll('.header, .fixed-element');

   if (!openButtons.length || !modalOverlay || !modalContent) return;

   // –ü–†–ò–ú–£–°–û–í–ò–ô –º–µ—Ç–æ–¥ –æ–±—á–∏—Å–ª–µ–Ω–Ω—è —à–∏—Ä–∏–Ω–∏ —Å–∫—Ä–æ–ª–±–∞—Ä—É
   function getScrollbarWidth() {
      const outer = document.createElement('div');
      outer.style.visibility = 'hidden';
      outer.style.overflow = 'scroll';
      document.body.appendChild(outer);
      const inner = document.createElement('div');
      outer.appendChild(inner);
      const width = outer.offsetWidth - inner.offsetWidth;
      outer.parentNode.removeChild(outer);
      return width;
   }

   function openModal() {
      const scrollWidth = getScrollbarWidth();

      // –í—Å—Ç–∞–Ω–æ–≤–ª—é—î–º–æ CSS-–∑–º—ñ–Ω–Ω—É –¥–ª—è –≤—Å—å–æ–≥–æ –¥–æ–∫—É–º–µ–Ω—Ç–∞
      document.documentElement.style.setProperty('--lock-padding', `${scrollWidth}px`);

      document.body.classList.add('no-scroll');
      modalOverlay.classList.add('is-open');
      modalOverlay.setAttribute('aria-hidden', 'false');
   }

   function closeModal() {
      modalOverlay.classList.remove('is-open');
      modalOverlay.setAttribute('aria-hidden', 'true');
      // –ö–ª–∞—Å no-scroll —ñ –∑–º—ñ–Ω–Ω—É –≤–∏–¥–∞–ª—è—î–º–æ –ü–Ü–°–õ–Ø –∑–∞–≤–µ—Ä—à–µ–Ω–Ω—è –∞–Ω—ñ–º–∞—Ü—ñ—ó (transitionend –≤–∂–µ —î –Ω–∏–∂—á–µ)
   }

   modalOverlay.addEventListener('transitionend', (e) => {
      // –ü–µ—Ä–µ–≤—ñ—Ä—è—î–º–æ, —â–æ –∞–Ω—ñ–º—É–≤–∞–≤—Å—è —Å–∞–º–µ –æ–≤–µ—Ä–ª–µ–π (opacity), –∞ –Ω–µ –≤–Ω—É—Ç—Ä—ñ—à–Ω—ñ –µ–ª–µ–º–µ–Ω—Ç–∏
      if (e.target === modalOverlay && !modalOverlay.classList.contains('is-open')) {
         document.body.classList.remove('no-scroll');
         document.documentElement.style.removeProperty('--lock-padding');
      }
   });

   openButtons.forEach(btn => {
      btn.addEventListener('click', async (e) => {
         e.preventDefault();

         // 1. –Ø–∫—â–æ –∫–æ–Ω—Ç–µ–Ω—Ç —â–µ –Ω–µ –∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–∏–π
         if (modalContent.innerHTML.trim() === '') {
            try {
               // –ü–æ–∫–∞–∑—É—î–º–æ –ª–æ–∞–¥–µ—Ä –ø—Ä—è–º–æ —É –≤—ñ–∫–Ω—ñ –ø–µ—Ä–µ–¥ —Ç–∏–º, —è–∫ –≤—ñ–¥–∫—Ä–∏—Ç–∏ –π–æ–≥–æ
               modalContent.innerHTML = '<div class="modal-loader">Loading...</div>';

               // –í—ñ–¥–∫—Ä–∏–≤–∞—î–º–æ "–ø–æ—Ä–æ–∂–Ω—î" –≤—ñ–∫–Ω–æ –∑ –ª–æ–∞–¥–µ—Ä–æ–º (–∫–æ—Ä–∏—Å—Ç—É–≤–∞—á –±–∞—á–∏—Ç—å, —â–æ —â–æ—Å—å –≤—ñ–¥–±—É–≤–∞—î—Ç—å—Å—è)
               openModal();

               const response = await fetch('modal.html');
               if (!response.ok) throw new Error('–ü–æ–º–∏–ª–∫–∞ –∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—è');
               const html = await response.text();

               // 2. –ö–æ–ª–∏ –¥–∞–Ω—ñ –ø—Ä–∏–π—à–ª–∏ ‚Äî –∑–∞–º—ñ–Ω—é—î–º–æ –ª–æ–∞–¥–µ—Ä –Ω–∞ —Ñ–æ—Ä–º—É
               // –†–æ–±–∏–º–æ —Ü–µ –∑ –Ω–µ–≤–µ–ª–∏–∫–æ—é –∑–∞—Ç—Ä–∏–º–∫–æ—é –¥–ª—è –ø–ª–∞–≤–Ω–æ—Å—Ç—ñ
               setTimeout(() => {
                  modalContent.innerHTML = html;
               }, 100);

            } catch (err) {
               modalContent.innerHTML = '<p>–ü–æ–º–∏–ª–∫–∞ –∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—è —Ñ–æ—Ä–º–∏.</p>';
            }
         } else {
            // –Ø–∫—â–æ –∫–æ–Ω—Ç–µ–Ω—Ç –≤–∂–µ –∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–∏–π —Ä–∞–Ω—ñ—à–µ ‚Äî –ø—Ä–æ—Å—Ç–æ –≤—ñ–¥–∫—Ä–∏–≤–∞—î–º–æ
            openModal();
         }
      });
   });

   modalOverlay.addEventListener('click', (e) => {
      if (e.target === modalOverlay || e.target.closest('[data-modal-close]')) {
         closeModal();
      }
   });

   document.addEventListener('keydown', (e) => {
      if (e.key === 'Escape' && modalOverlay.classList.contains('is-open')) {
         closeModal();
      }
   });
}




// ===========================================================================================
// -----------------------------
// accordions
// -----------------------------
document.addEventListener('click', (e) => {
   const btn = e.target.closest('[data-accordion-btn]');
   if (!btn) return;

   const accordionParent = btn.closest('[data-accordion]');
   if (!accordionParent) return;

   const body = accordionParent.querySelector('[data-accordion-body]');
   const icon = btn.querySelector('[data-accordion-icon]');
   if (!body) return;

   const isOpen = accordionParent.classList.contains('active');

   if (!isOpen) {
      accordionParent.classList.add('active');
      if (icon) icon.classList.add('icon-active');

      body.style.height = body.scrollHeight + 'px';

      body.addEventListener(
         'transitionend',
         () => {
            if (accordionParent.classList.contains('active')) {
               body.style.height = 'auto';
            }
         },
         { once: true }
      );
   } else {
      body.style.height = body.scrollHeight + 'px';
      body.offsetHeight;

      requestAnimationFrame(() => {
         accordionParent.classList.remove('active');
         if (icon) icon.classList.remove('icon-active');
         body.style.height = '0px';
      });
   }
});
