function initScrollHeader() {
   const header = document.querySelector('.header');
   if (!header) return;

   // 1. –Ü–Ω—ñ—Ü—ñ–∞–ª—ñ–∑–∞—Ü—ñ—è: –≤—Å—Ç–∞–Ω–æ–≤–ª—é—î–º–æ —Å—Ç–∞–Ω –≤—ñ–¥—Ä–∞–∑—É –ø—Ä–∏ –∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—ñ
   let currentScroll = window.scrollY;
   let lastScroll = currentScroll;
   let downStart = currentScroll;

   // –Ø–∫—â–æ —Å—Ç–æ—Ä—ñ–Ω–∫–∞ –≤–∂–µ –ø—Ä–æ—Å–∫—Ä–æ–ª–µ–Ω–∞ –ø—Ä–∏ –∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—ñ (–ø—ñ—Å–ª—è –æ–Ω–æ–≤–ª–µ–Ω–Ω—è)
   if (currentScroll > 50) {
      header.classList.add('scrolled');
      // –ú–æ–∂–Ω–∞ –¥–æ–¥–∞—Ç–∏ 'visible', —â–æ–± —Ö–µ–¥–µ—Ä –±—É–≤ –≤—ñ–¥—Ä–∞–∑—É –ø—Ä–∏ –æ–Ω–æ–≤–ª–µ–Ω–Ω—ñ,
      // –∞–±–æ –∑–∞–ª–∏—à–∏—Ç–∏ –ø—Ä–∏—Ö–æ–≤–∞–Ω–∏–º –¥–æ –ø–µ—Ä—à–æ–≥–æ —Ä—É—Ö—É –≤–≥–æ—Ä—É
      header.classList.add('visible');
   }

   const OFFSET = 50;
   const DELTA = 8;
   const HIDE_AFTER = 40;

   const onScroll = () => {
      const current = window.scrollY;

      // 2. –ó–ê–•–ò–°–¢ –í–Ü–î FOOTER: –ø–µ—Ä–µ–≤—ñ—Ä—è—î–º–æ, —á–∏ –º–∏ –≤ —Å–∞–º–æ–º—É –Ω–∏–∑—É
      // –Ø–∫—â–æ –¥–æ –Ω–∏–∑—É —Å—Ç–æ—Ä—ñ–Ω–∫–∏ –∑–∞–ª–∏—à–∏–ª–æ—Å—å –º–µ–Ω—à–µ 20px, —ñ–≥–Ω–æ—Ä—É—î–º–æ –ª–æ–≥—ñ–∫—É –ø–æ—è–≤–∏
      const scrollHeight = document.documentElement.scrollHeight;
      const screenHeight = window.innerHeight;
      const isBottom = current + screenHeight >= scrollHeight - 20;

      // –ó–∞—Ö–∏—Å—Ç: –º–µ–Ω—é –≤—ñ–¥–∫—Ä–∏—Ç–µ –∞–±–æ iOS "–≤—ñ–¥—Å–∫–æ–∫" (negative scroll)
      if (document.documentElement.classList.contains('menu-open') ||
         header.classList.contains('menu-open') ||
         current < 0) {
         return;
      }

      const diff = current - lastScroll;
      if (Math.abs(diff) < DELTA) return;

      // –í–µ—Ä—Ö —Å—Ç–æ—Ä—ñ–Ω–∫–∏ (–ø–æ–≤–Ω–µ —Å–∫–∏–¥–∞–Ω–Ω—è)
      if (current <= OFFSET) {
         header.classList.remove('scrolled', 'visible');
         header.style.transform = '';
         downStart = current;
         lastScroll = current;
         return;
      }

      // –°–∫—Ä–æ–ª –≤–Ω–∏–∑
      if (diff > 0) {
         if (current - downStart > HIDE_AFTER) {
            header.classList.add('scrolled');
            header.classList.remove('visible');
         }
      }
      // –°–∫—Ä–æ–ª –≤–≥–æ—Ä—É (—Ç—ñ–ª—å–∫–∏ —è–∫—â–æ –º–∏ –ù–ï –≤ —Å–∞–º–æ–º—É –Ω–∏–∑—É)
      else {
         if (!isBottom) {
            header.classList.add('scrolled', 'visible');
         }
         downStart = current;
      }

      lastScroll = current;
   };

   // throttle –¥–ª—è –æ–ø—Ç–∏–º—ñ–∑–∞—Ü—ñ—ó (–æ–ø—Ü—ñ–æ–Ω–∞–ª—å–Ω–æ, –∞–ª–µ scroll —ñ —Ç–∞–∫ –ø—Ä–∞—Ü—é—î —á–∞—Å—Ç–æ)
   window.addEventListener('scroll', onScroll, { passive: true });
}


// ===========================================================================================
// -----------------------------
// MENU-BURGER
// -----------------------------
function documentActions(e) {
   const targetElement = e.target
   if (targetElement.closest('.icon-menu')) {
      document.body.classList.toggle('menu-open')
      document.body.classList.toggle('scroll-lock')
      document.documentElement.classList.toggle('menu-open')
   }
}

